  }
  
    
    
  
  
  /* scroll to top button */
  .top-btn-position {
    position: fixed;
    bottom: 15px;
    right: 20px;
  }
  
  .top-btn-style {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    border: 2px solid #ff105e;
    background-color: #fff;
    color:#ff105e;
    cursor: pointer;
  }
  
  .top-btn-style:hover {
    animation: none;
    background-color:#ff105e;
    color: #fff;
    border: 2px solid #fff;
  }
  
  
  